n                    window.location.href = `/directory/vendors/${filteredObject[0].path}/filtered?type=${item.slug}`;\n\n                }\n            }else{\n                window.location.href = `/directory/vendors/venues/filtered?type=${item.name}`;\n            } \n        }\n    },\n    mounted() {\n        document.onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                this.setSplider();\n            }\n        };\n    },\n\n}\n</script>\n\n<style scoped>\n\n.vendor-cats-cont{\n    background: #F4F4F4;\n    padding: 54px 0;\n    position: relative;\n}\n.vendor-cats-cont > h1 {\n    color: #000;\nfont-family: Poppins;\nfont-size: 18px;\nfont-style: normal;\nfont-weight: 500;\nline-height: normal;\nmargin-bottom: 15px;\n}\n#categories-slider a {\n    text-decoration: none;\n    height: 100%;\n    display: flex;\n    width: 114.128px;\n    padding: 14.106px;\n    flex-direction: column;\n    align-items: center;\n    justify-content: flex-start;\n    gap: 8.153px;\n    border-radius: 7.694px;\n    background: #FFF;\n    cursor: pointer;\n}\n\n#categories-slider span {\n    color: #000;\n    text-align: center;\n    font-family: Poppins;\n    font-size: 16.62px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n}\n.vendor-common-cont {\n    padding: 54px 0;\n}\n.vendor-common-cont > div:nth-child(1) {\n    display: flex;\n    justify-content: space-between;\n}\n.vendor-common-cont > div:nth-child(1) > h1 {\n    color: #000;\n    font-family: Poppins;\n    font-size: 18px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    margin-bottom: 15px;\n}\n.vendor-common-cont > div:nth-child(1) > a {\n    display: block;\n    color: #461952;\n    font-family: Poppins;\n    font-size: 14.088px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n    text-decoration-line: underline;\n    margin-bottom: 15px;\n}\n\n</style>\n<style>\n.categories-slider 