:title=\"'Wedding Vendors'\" :description=\"'Browse, shortlist and get best prices and packages from venues available to host your upcoming wedding reception,pre-wedding and post-wedding functions.'\" :cities=\"cities\" :categories=\"subcat\"></searchHeader>\n        <div class=\"vendor-cats-cont\">\n            <h1 class=\"container\">Search By: <b>Category</b></h1>\n            <div id=\"categories-slider\" class=\"container splide\">\n                <div class=\"splide__arrows\">\n                        <button class=\"splide__arrow splide__arrow--prev\">\n                            <img src=\"/assets/chevron-left.svg\" alt=\"\">\n                        </button>\n                        <button class=\"splide__arrow splide__arrow--next\">\n                            <img src=\"/assets/chevron-right.svg\" alt=\"\">\n                        </button>\n                </div>\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list\">\n                        <li\n                            v-for=\"(item, index) in subcat\"\n                            :key=\"index\"\n                            style=\"\n                                margin-right: 12px;\n                                border: none !important;\n                            \"\n                            class=\"splide__slide\"\n                        >\n                            <a\n                                @click=\"startLink(item)\"\n                                class=\"single-venue-cat\"\n                            >\n                                <img\n                                    :src=\"\n                                        '/assets/directory/icons/' +\n                                        item.icon\n                                    \"\n                                    alt=\"\"\n                                />\n                                <span>{{ item.name }}</span>\n                            </a>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n        <div class=\"container vendor-common-cont\">\n            <div>\n                <h1>Top Vendors</h1>\n                <a href=\"\">View all</a>\n            </div>\n            <div id=\"top-vendors\" class=\"splide\">\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list single-card-outer\">\n                        <li\n                            v-for=\"(item, index) in subcat\"\n                            :key=\"index\"\n                            style=\"\n                                max-width: 300px;\n                                width: 100%;\n                                min-width: 250px;\n                                margin-right: 12px;\n                                border: none !important;\n                            \"\n                            class=\"splide__slide\"\n                        >\n                            <makeupCard :item=\"item\"></makeupCard>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n        <div class=\"container vendor-common-cont\">\n            <div>\n                <h1>Featured Vendors</h1>\n                <a href=\"\">View all</a>\n            </div>\n            <div id=\"featured-vendors\" class=\"splide\">\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list single-card-outer\">\n                        <li\n                            v-for=\"(item, index) in subcat\"\n                            :key=\"index\"\n                            style=\"\n                                max-width: 300px;\n                                width: 100%;\n                                min-width: 250px;\n                                margin-right: 12px;\n                                border: none !important;\n                            \"\n                            class=\"splide__slide\"\n                        >\n                            <makeupCard :item=\"item\"></makeupCard>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n        <div class=\"container vendor-common-cont\">\n            <div>\n                <h1>Popular Searches</h1>\n                <a href=\"\">View all</a>\n            </div>\n            <div id=\"popular-vendors\" class=\"splide\">\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list single-card-outer\">\n                        <li\n                            v-for=\"(item, index) in subcat\"\n                            :key=\"index\"\n                            style=\"\n                                max-width: 300px;\n                                width: 100%;\n                                min-width: 250px;\n                                margin-right: 12px;\n                                border: none !important;\n                            \"\n                            class=\"splide__slide\"\n                        >\n                            <makeupCard :item=\"item\"></makeupCard>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n<script>\nimport Multiselect from \"vue-multiselect\";\nimport makeupCard from \"./commonComponents/makeupCard.vue\";\nimport searchHeader from \"./commonComponents/searchHeader.vue\";\nexport default {\n    components: {\n        Multiselect,\n        makeupCard,\n        searchHeader\n    },\n    props: [\"cities\", \"subcat\"],\n    data(){\n        return {\n            selectedVenue: null,\n            selectedCity: null,\n            cont: {},\n            ids: [\n                \"categories-slider\",\n                \"top-vendors\",\n                'popular-vendors',\n                \"featured-vendors\"\n\n            ],\n        }\n    },\n    methods:{\n        setSplider() {\n            let _this = this;\n            this.$nextTick(function () {\n                this.ids.forEach(function (item, index) {\n                    _this.cont[item] = new Splide(`#${item}`, {\n                        gap: 5,\n                        rewind: true,\n                        pagination: false,\n                        cover: true,\n                        // isNavigation: true,\n                        arrows: true,\n                        autoWidth: true,\n                        breakpoints: {\n                            567: {\n                                arrows: false,\n                                wheel: true,\n                                direction: \"ltr\",\n                                releaseWheel: false,\n                            },\n                        },\n                    });\n                    _this.cont[item].mount();\n                });\n            });\n        },\n        startLink(item){\n            if(item.path != null && item.path != ''){\n                if(item.parent_id == null){\n                    window.location.href = `/directory/vendors/${item.path}`;\n                }else{\n                    const filteredObject = this.subcat.filter(object => object.id == item?.parent_id); \n                    //console.log(filteredObject);\n                    window.location.href = `/directory/vendors/${filteredObject[0].path}/filtered?type=${item.slug}`;\n\n                }\n            }else{\n                window.location.href = `/directory/vendors/venues/filtered?type=${item.name}`;\n            } \n        }\n    },\n    mounted() {\n        document.onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                this.setSplider();\n            }\n        };\n    },\n\n}\n</script>\n\n<style scoped>\n\n.vendor-cats-cont{\n    background: #F4F4F4;\n    padding: 54px 0;\n    position: relative;\n}\n.vendor-cats-cont > h1 {\n    color: #000;\nfont-family: Poppins;\nfont-size: 18px;\nfont-style: normal;\nfont-weight: 500;\nline-height: normal;\nmargin-bottom: 15px;\n}\n#categories-slider a {\n    text-decoration: none;\n    height: 100%;\n    display: flex;\n    width: 114.128px;\n    padding: 14.106px;\n    flex-direction: column;\n    align-items: center;\n    justify-content: flex-start;\n    gap: 8.153px;\n    border-radius: 7.694px;\n    background: #FFF;\n    cursor: pointer;\n}\n\n#categories-slider span {\n    color: #000;\n    text-align: center;\n    font-family: Poppins;\n    font-size: 16.62px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n}\n.vendor-common-cont {\n    padding: 54px 0;\n}\n.vendor-common-cont > div:nth-child(1) {\n    display: flex;\n    justify-content: space-between;\n}\n.vendor-common-cont > div:nth-child(1) > h1 {\n    color: #000;\n    font-family: Poppins;\n    font-size: 18px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    margin-bottom: 15px;\n}\n.vendor-common-cont > div:nth-child(1) > a {\n    display: block;\n    color: #461952;\n    font-family: Poppins;\n    font-size: 14.088px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n    text-decoration-line: underline;\n    margin-bottom: 15px;\n}\n\n</style>\n<style>\n.categories-slider .splide__arrow{\n    background:none !important;\n}\n.categories-slider .splide__arrow--prev {\n    position: absolute;\n    top: -28px;\n    right: 45px;\n    left: unset;\n}\n.categories-slider .splide__arrow--next {\n    position: absolute;\n    top: -28px;\n    right: 10px;\n    left: unset;\n}\n</style>"],"sourceRoot":""}]);
